import { useState } from "react";
import { Link, Route, Routes, useParams } from "react-router-dom";
import notice from "../db/notice_list.json";
import useFetch from "../hooks/useFetch";
import Pagination from "./Pagination";

export default function Noticelistall(props) {
    const param = useParams();
    const [listCnt, setListCnt] = useState(12);
    const noticeList = notice.lists.filter((list, index) => (
            list = index < (listCnt * Number(param.pageno)) && index >= (listCnt * Number(param.pageno)) - listCnt
        )
    )
    function changeListCnt(cnt) {
        if(listCnt != cnt){
            setListCnt(cnt);
            Location.href = '/noticelistall/1';
        }
    }
    const pageCnt = Math.floor(notice.totalCnt / listCnt) + Number(1);
    return (
        <>
            <div className="board_allview">
                <div className="board_name">Notice All List
                    <div className="board_sort_listCnt display_none">
                        <div className="selectedCnt">{listCnt} </div>
                        <ul>
                            <li><button type="button" className="btn_sort_listCnt" onClick={(e) => {e.preventDefault(); changeListCnt(6)} }>6 </button></li>
                            <li><button type="button" className="btn_sort_listCnt" onClick={(e) => {e.preventDefault(); changeListCnt(12)}}>12 </button></li>
                            <li><button type="button" className="btn_sort_listCnt" onClick={(e) => {e.preventDefault(); changeListCnt(24)}}>24 </button></li>
                        </ul>
                    </div>
                </div>
                <ul className="board_list">
                    {noticeList.map((list, index) => (
                        <li key={index}>
                            <Link to={`/noticeview/${list.NUM}`}>
                                <div className="board_date">{list.REGDATE}</div>
                                <div className="board_subject">{list.TITLE}</div>
                                <div className="board_content">{list.CONTENT}</div>
                            </Link>
                        </li>
                    ))}
                </ul>
                <Pagination pageCnt={pageCnt} pageNo={param.pageno} />
            </div>
        </>
    );
}