import { BrowserRouter, Routes, Route } from 'react-router-dom';
import { useState } from 'react';
import './index.css';
import Header from "./component/Header";
import Mainindex from './pages/Mainindex';
import EmptyPage from './component/EmptyPage';
import Newsview from './component/Newsview';
import Noticeview from './component/Noticeview';
import Newslistall from './component/Newslistall';
import Noticelistall from './component/Noticelistall';


function App() {
  const [currentPage, setCurrentPage] = useState(0);
  function pageNumber(value) {
    setCurrentPage(value);
    // console.log(' sdiofjaowfjeoajiw e')
  }

  return (
    <BrowserRouter basename={process.env.PUBLIC_URL}>
      <div className="App">
        <Header pageNumber={pageNumber} selectedPageNumber={currentPage}></Header>
        <Routes>
          <Route path="/" element={<Mainindex pageNumber={pageNumber} selectedPageNumber={currentPage} />} > </Route>
          <Route path="/newslistall/:pageno" element={<Newslistall />} />
          <Route path="/newsview/:no" element={<Newsview />} />
          <Route path="/noticelistall/:pageno" element={<Noticelistall />} />
          <Route path="/noticeview/:no" element={<Noticeview />} />
          <Route path="/newsview/*" element={<EmptyPage />} />
          <Route path="/noticeview/*" element={<EmptyPage />} />
          <Route path="*" element={<EmptyPage />} />
        </Routes>
      </div>
    </BrowserRouter>
  );
}
export default App;