import { useState } from "react";
import { Link, Route, Routes } from "react-router-dom";
import notice from "../db/notice_list.json";

export default function Noticelist() {
    const noticeList = notice.lists.filter((list, index) => (
            list = index < 9
        )
    )

    return (
        <>
            <ul className="board_list noSwiping">
                {noticeList.map((list, index) => (
                    <li key={index}>
                        <Link to={`/noticeview/${list.NUM}`}>
                            <div className="board_date">{list.REGDATE}</div>
                            <div className="board_subject">{list.TITLE}</div>
                            <div className="board_content">{list.CONTENT}</div>
                        </Link>
                    </li>
                ))}
            </ul>
            <div className="common_btn_warp text_center">
                <Link to='/noticelistall/1' className="common_btn btn_blue btn_listallview"></Link>
            </div>
        </>
    );
}