import React, { useRef } from "react";
import { Swiper, SwiperSlide } from 'swiper/react';
import SwiperCore, { Pagination } from "swiper";
import 'swiper/css';
// import "swiper/css/navigation";
// import "swiper/css/pagination";

SwiperCore.use([ Pagination ]);

export default function Slide3() {
    const swiperRef = useRef(null);
    function slidePrev() {
        swiperRef.current.swiper.slidePrev();
    }
    function slideNext() {
        swiperRef.current.swiper.slideNext();
    }
    return (
        <>
            <div className="swiper_inner">
                <div className="fixedtit">
                    <div className='txt_1 trnasition_fadeInUp trnasition_delay_5'>Business</div>
                    <div className='txt_2 trnasition_fadeInUp trnasition_delay_10'>
                             <br />
                            
                    </div>
                </div>
                <div className="businessSwiper trnasition_fadeInUp trnasition_delay_15">
                    <Swiper
                        ref={swiperRef}
                        spaceBetween={50}
                        slidesPerView={1}
                        speed={1000}
                        pagination={true}
                        modules={[ Pagination ]}
                    >
                        <SwiperSlide><div className="businessslide business_1"></div></SwiperSlide>
                        <SwiperSlide><div className="businessslide business_2"></div></SwiperSlide>
                        <SwiperSlide><div className="businessslide business_3"></div></SwiperSlide>
                        <SwiperSlide><div className="businessslide business_4"></div></SwiperSlide>
                        <SwiperSlide><div className="businessslide business_5"></div></SwiperSlide>
                    </Swiper>
                    <button type="button" className="btn_slide_custom btn_slide_prev" onClick={slidePrev}></button>
                    <button type="button" className="btn_slide_custom btn_slide_next" onClick={slideNext}></button>
                </div>
			</div>
        </>
    )
}