import { useEffect, useState } from "react";
import { Link, useLocation, useParams } from "react-router-dom";
import news from "../db/news_list.json";

export default function Newsview() {
    const param = useParams();
    const thisNews = news.lists.filter(list => (
        list.NUM === Number(param.no)
    ))
    const prevNews = news.lists.filter(list => (
        list.NUM === Number(param.no) - Number(1)
    ))
    const nextNews = news.lists.filter(list => (
        list.NUM === Number(param.no) + Number(1)
    ))
    
    const reactStringReplace = require('react-string-replace');

    // console.log(thisNews[Object.keys(thisNews)[0]]); value
    return (
        <>
            <div className="boardView boardNews">
                <div className="boardView_detail">
                    <div className="subject">
                        {thisNews[0].TITLE}
                        <div className="date">{thisNews[0].REGDATE}</div>
                    </div>
                    <div className="content">
                        {reactStringReplace(thisNews[0].CONTENT , '\n', (match, i) => (
                            <br key={i} />
                        ))}
                    </div>
                    <Link to="/" className="btn_to_list"></Link>
                </div>
                <div className="board_navigation">
                    <Link to={`/newsview/${prevNews[0] ? prevNews[0].NUM : ''}`}>
                        <span className="navi_prefix"></span>
                        <span className="navi_title">{prevNews[0] ? prevNews[0].TITLE : ' '}</span>
                        <span className="navi_date">{prevNews[0] ? prevNews[0].REGDATE : ''}</span>
                    </Link>
                    <Link to={`/newsview/${nextNews[0] ? nextNews[0].NUM : ''}`}>
                        <span className="navi_prefix"></span>
                        <span className="navi_title">{nextNews[0] ? nextNews[0].TITLE : ' '}</span>
                        <span className="navi_date">{nextNews[0] ? nextNews[0].REGDATE : ''}</span>
                    </Link>
                </div>
                
            </div>
        </>
    );
}