import React from "react";
import { Link } from "react-router-dom";

export default function Gnblist( props ) {
    // const classOn = document.querySelector('.gnbmenu ul li').dataset.page === props.selectedPageNumber ?  "on" : "";
    function handleClick(e) {
        if(!Location.pathname === undefined) {
            Location.href = '/';
        } else {
            props.pageNumber(e.target.dataset.page)
        }
    }
    
    if(props.selectedPageNumber != 'undefined' || props.selectedPageNumber != 'null') {
        const gnbList = document.querySelectorAll('.gnbmenu ul li');
        for (let i=0; i<gnbList.length; i++) {
            gnbList[i].querySelector('a').classList.remove('on');
            gnbList[props.selectedPageNumber].querySelector('a').classList.add('on');
        }
    }
    
    return (
        <nav className="gnbmenu">
            <ul className="flex_box">
                <li><Link to="/" onClick={handleClick} data-page="0">Home</Link></li>
                <li><Link to="/" onClick={handleClick} data-page="1">About Us</Link></li>
                <li><Link to="/" onClick={handleClick} data-page="2">Business</Link></li>
                <li><Link to="/" onClick={handleClick} data-page="3">News</Link></li>
                <li><Link to="/" onClick={handleClick} data-page="4">Notice</Link></li>
                <li><Link to="/" onClick={handleClick} data-page="5">CI</Link></li>
                <li><Link to="/" onClick={handleClick} data-page="6">Contact</Link></li>
            </ul>
            {/* <ul className="flex_box">
                <li onClick={handleClick} data-page="0"><Link to="/#slide1">Home</Link></li>
                <li onClick={handleClick} data-page="1"><Link to="/#slide2">About Us</Link></li>
                <li onClick={handleClick} data-page="2"><Link to="/#slide3">Business</Link></li>
                <li onClick={handleClick} data-page="3"><Link to="/#slide4">News</Link></li>
                <li onClick={handleClick} data-page="4"><Link to="/#slide5">Notice</Link></li>
                <li onClick={handleClick} data-page="5"><Link to="/#slide6">CI</Link></li>
                <li onClick={handleClick} data-page="6"><Link to="/#slide7">Contact</Link></li>
            </ul> */}
        </nav>
    )
}