import { useEffect, useState } from "react";
import { Link, useLocation, useParams } from "react-router-dom";
import notice from "../db/notice_list.json";

export default function Noticeview() {
    const param = useParams();
    const thisNotice = notice.lists.filter(list => (
        list.NUM === Number(param.no)
    ))
    const prevNotice = notice.lists.filter(list => (
        list.NUM === Number(param.no) - Number(1)
    ))
    const nextNotice = notice.lists.filter(list => (
        list.NUM === Number(param.no) + Number(1)
    ))
    
    const reactStringReplace = require('react-string-replace');

    
    return (
        <>
            <div className="boardView boardNotice">
                <div className="boardView_detail">
                    <div className="subject">
                        {thisNotice[0].TITLE}
                        <div className="date">{thisNotice[0].REGDATE}</div>
                    </div>
                    <div className="content">
                        {reactStringReplace(thisNotice[0].CONTENT , '\n', (match, i) => (
                            <br key={i} />
                        ))}
                    </div>
                    <Link to="/" className="btn_to_list"></Link>
                </div>
                <div className="board_navigation">
                    <Link to={`/noticeview/${prevNotice[0] ? prevNotice[0].NUM : ''}`}>
                        <span className="navi_prefix"></span>
                        <span className="navi_title">{prevNotice[0] ? prevNotice[0].TITLE : ' '}</span>
                        <span className="navi_date">{prevNotice[0] ? prevNotice[0].REGDATE : ''}</span>
                    </Link>
                    <Link to={`/noticeview/${nextNotice[0] ? nextNotice[0].NUM : ''}`}>
                        <span className="navi_prefix"></span>
                        <span className="navi_title">{nextNotice[0] ? nextNotice[0].TITLE : ' '}</span>
                        <span className="navi_date">{nextNotice[0] ? nextNotice[0].REGDATE : ''}</span>
                    </Link>
                </div>
            </div>
        </>
    );
}