import { Link, Route, Routes } from "react-router-dom";
import news from "../db/news_list.json";
import useFetch from "../hooks/useFetch";

export default function Newslist() {
    const newsList = news.lists.filter((list, index) => (
            list = index < 9
        )
    )
    // const thisNews = useFetch(`http://localhost:3000/`);
    return (
        <>
            <ul className="board_list ">
                {newsList.map((list, index) => (
                    <li key={index}>
                        <Link to={`/newsview/${list.NUM}`}>
                            <div className="board_date">{list.REGDATE}</div>
                            <div className="board_subject">{list.TITLE}</div>
                            <div className="board_content">{list.CONTENT}</div>
                        </Link>
                    </li>
                ))}
            </ul>
            <div className="common_btn_warp text_center">
                <Link to='/newslistall/1' className="common_btn btn_blue btn_listallview"></Link>
            </div>
        </>
    );
}