import Gnblist from "./Gnblist";
import { Link } from "react-router-dom";

export default function Header(props) {
    function handleClick() {
        props.pageNumber(0)
    }
    
    return (
        <div id="header">
            <div className="container">
                <div className="flex_box">
                    <h1><Link to="/" onClick={handleClick}><img src="//img.enuri.info/images/sample/intro/logo@170.png" /></Link></h1>
                    <Gnblist pageNumber={props.pageNumber} selectedPageNumber={props.selectedPageNumber}></Gnblist>
                </div>
            </div>
        </div>
    );
}