import React, { useRef, useEffect } from "react";
import SwiperCore, {
    HashNavigation,
    Mousewheel,
    A11y
} from "swiper";

import { Swiper, SwiperSlide } from 'swiper/react';
import "swiper/css";
import "../component/main.css";
import Slide1 from "./Slide/Slide1";
import Slide2 from "./Slide/Slide2";
import Slide3 from "./Slide/Slide3";
import Slide4 from "./Slide/Slide4";
import Slide5 from "./Slide/Slide5";
import Slide6 from "./Slide/Slide6";
import Slide7 from "./Slide/Slide7";

SwiperCore.use([ HashNavigation, Mousewheel, A11y]);


export default function Mainindex(props) {
    const swiperRef = useRef(null);
    useEffect(() => {
        swiperRef.current.swiper.slideTo(props.selectedPageNumber)
    });
    function testIndex() {
        props.pageNumber(swiperRef.current.swiper.activeIndex)
        // console.log(swiperRef.current.swiper.activeIndex);
    }
    // 
    // useEffect(() => {
    //     if(!swiperRef.current) {
    //         swiperRef = true;
    //     } else {
    //         console.log(swiperRef.current.swiper.activeIndex);
    //         swiperRef.current.swiper.slideTo(props.selectedPageNumber)
    //         props.pageNumber(swiperRef.current.swiper.activeIndex)
    //     }
    // }, [swiperRef.current.swiper.activeIndex]);

    return (
        <>
            <Swiper
                ref={swiperRef}
                direction={"vertical"}
                speed={1500}
                mousewheel={true}
                hashNavigation={{
                    watchState: true,
                }}
                noSwiping={true}
                noSwipingClass="noSwiping"
                modules={[ Mousewheel ]}
                watchSlidesProgress
                onSlideChange={(swiper) => testIndex()}
                // onSwiper={(swiper) => console.log("swiperoo", swiper)}
                className="mySwiper"
            >
                <SwiperSlide data-hash="slide1" className="slide slide1"><Slide1 /></SwiperSlide>
                <SwiperSlide data-hash="slide2" className="slide slide2"><Slide2 /></SwiperSlide>
                <SwiperSlide data-hash="slide3" className="slide slide3"><Slide3 /></SwiperSlide>
                <SwiperSlide data-hash="slide4" className="slide slide4"><Slide4 /></SwiperSlide>
                <SwiperSlide data-hash="slide5" className="slide slide5"><Slide5 /></SwiperSlide>
                <SwiperSlide data-hash="slide6" className="slide slide6"><Slide6 /></SwiperSlide>
                <SwiperSlide data-hash="slide7" className="slide slide7"><Slide7 /></SwiperSlide>
            </Swiper>
        </>
    );
}