import { useState } from "react";
import { Link, Route, Routes, useParams } from "react-router-dom";
import news from "../db/news_list.json";

export default function Pagination(props) {
    const pageRender = () => {
        const result = [];
        for (let i=1; i < props.pageCnt+1; i++) {
            result.push(<Link to={`/newslistall/${i}`} className={Number(props.pageNo) === i ? 'active' : ''} key={i}>{i}</Link>);
        }
        return result;
    };

    return (
        <>
            <div className="pagination">{pageRender()}</div>
        </>
    );
}